import React from "react";
import {
	CashStack,
	Hdmi,
	HeartPulse,
	Hospital,
	PersonBadge,
	Robot,
} from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const AdminDash = () => {
	const navigate = useNavigate();
	return (
		<>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				{" "}
				<div
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
					style={{ zIndex: 1000 }}
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<HeartPulse className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Services</p>
							</div>
						</Dropdown.Toggle>
						<Dropdown.Menu>
							<Dropdown.Item>
								<p
									className="btn-link"
									onClick={() => {
										window.scroll(0, 0);
										navigate(`service-categories`);
									}}
								>
									Service Categories
								</p>
							</Dropdown.Item>

							<Dropdown.Item>
								<p
									className="btn-link"
									onClick={() => {
										window.scroll(0, 0);
										navigate(`services`);
									}}
								>
									Services
								</p>
							</Dropdown.Item>
						</Dropdown.Menu>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						navigate(`staffs`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<PersonBadge className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Staffs</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						navigate(`modes`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<CashStack className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Payment Modes
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>

			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						navigate(`wards`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Hospital className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Wards</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						navigate(`beds`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Hdmi className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Ward Beds</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						navigate(`insurance-companies`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Robot className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Accepted Insurance
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default AdminDash;
