import React from "react";
import { Dropdown } from "react-bootstrap";
import { Book, CashCoin } from "react-bootstrap-icons";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
const ClientDash = () => {
	const Navigate = useNavigate();
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	return (
		<>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						Navigate(`patients/patient/finance/${ActiveCred.linker}`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<CashCoin className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Finances</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				<div
					onClick={() => {
						window.scroll(0, 0);
						Navigate(`patients/patient/health/${ActiveCred.linker}`);
					}}
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Book className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Health Records
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default ClientDash;
