import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import AdminDash from "../Admin/AdminDash";
import FinanceDash from "../Finance/FinanceDash";
import { useNavigate } from "react-router-dom";
import PharmacyDash from "../Pharmacy/PharmacyDash";
import StoreDash from "../Store/StoreDash";
import { Bell, EnvelopeOpenHeart, PersonHearts } from "react-bootstrap-icons";
import { FetchLiveSubs } from "../../Network/SubApi";
import ClientDash from "../Client/ClientDash";
import { VerifySub } from "../../Utils/VerifySub";
import { Dropdown } from "react-bootstrap";

const HomeDash = () => {
	//redux dispatch
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {
		FetchLiveSubs(User.token, ActiveCred.instLinker, 0, dispatch);
	}, []);

	const inst = useSelector((state) => state.inst.activeInst);
	const User = useSelector((state) => state.auth.user);
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	const subs = useSelector((state) => state.sub.subs);
	const Sub = VerifySub(subs, ActiveCred.instLinker);

	return Sub > 0 ? (
		<div className="row m-3 justify-content-center">
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				{parseInt(ActiveCred.admin) === 1 && inst.sub === "Kenya" ? (
					<div
						className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
						style={{ zIndex: 1000 }}
					>
						<Dropdown className="no-caret" style={{ width: "100%" }}>
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<Bell className="dash-icon" />
									</div>
									<p className="text-center text-wrap dash-text  ">
										Notifications
									</p>
								</div>
							</Dropdown.Toggle>
							<Dropdown.Menu>
								<Dropdown.Item>
									<p
										className="btn-link"
										onClick={() => {
											window.scroll(0, 0);
											navigate(`notifications`);
										}}
									>
										General Notifications
									</p>
								</Dropdown.Item>

								<Dropdown.Item>
									<p
										className="btn-link"
										onClick={() => {
											window.scroll(0, 0);
											navigate(`bulk-sms`);
										}}
									>
										SMS Notifications
									</p>
								</Dropdown.Item>
							</Dropdown.Menu>
						</Dropdown>
					</div>
				) : (
					<div
						onClick={() => {
							window.scroll(0, 0);
							navigate(`notifications`);
						}}
						className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
					>
						<Dropdown className="no-caret" style={{ width: "100%" }}>
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<Bell className="dash-icon" />
									</div>
									<p className="text-center text-wrap dash-text  ">
										Notifications
									</p>
								</div>
							</Dropdown.Toggle>
						</Dropdown>
					</div>
				)}
			</div>
			{parseInt(ActiveCred.admin) === 1 ? <AdminDash></AdminDash> : null}
			{parseInt(ActiveCred.admin) === 1 ||
			parseInt(ActiveCred.finance) === 1 ? (
				<FinanceDash></FinanceDash>
			) : null}
			{parseInt(ActiveCred.admin) === 1 ||
			parseInt(ActiveCred.finance) === 1 ||
			parseInt(ActiveCred.medical) === 1 ? (
				<>
					<PharmacyDash></PharmacyDash>
					<StoreDash></StoreDash>
					<div className="col-6 col-md-4 d-flex align-items-stretch ">
						<div
							onClick={() => {
								window.scroll(0, 0);
								navigate(`patients`);
							}}
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
						>
							<Dropdown className="no-caret" style={{ width: "100%" }}>
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<PersonHearts className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">
											Patients
										</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>{" "}
					<div className="col-6 col-md-4 d-flex align-items-stretch ">
						<div
							onClick={() => {
								window.scroll(0, 0);
								navigate(`appointments`);
							}}
							className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
						>
							<Dropdown className="no-caret" style={{ width: "100%" }}>
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<EnvelopeOpenHeart className="dash-icon" />
										</div>
										<p className="text-center text-wrap dash-text  ">
											Appointments
										</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>
				</>
			) : null}
			{parseInt(ActiveCred.patient) === 1 ? <ClientDash></ClientDash> : null}
		</div>
	) : (
		<></>
	);
};

export default HomeDash;
