import React from "react";
import { ShopWindow } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const PharmacyDash = () => {
	const navigate = useNavigate();
	return (
		<>
			<div className="col-6 col-md-4 d-flex align-items-stretch ">
				{" "}
				<div
					className="dashboard d-flex justify-content-center align-items-center mb-2 w-100 mb-2"
					style={{ zIndex: 1000 }}
				>
					<Dropdown className="no-caret" style={{ width: "100%" }}>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<ShopWindow className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Pharmacy</p>
							</div>
						</Dropdown.Toggle>
						<Dropdown.Menu>
							<Dropdown.Item>
								<p
									className="btn-link"
									onClick={() => {
										window.scroll(0, 0);
										navigate(`pharmacy-categories`);
									}}
								>
									Pharmacy Categories
								</p>
							</Dropdown.Item>

							<Dropdown.Item>
								<p
									className="btn-link"
									onClick={() => {
										window.scroll(0, 0);
										navigate(`pharmacy-products`);
									}}
								>
									Pharmacy Products
								</p>
							</Dropdown.Item>
							<Dropdown.Item>
								<p
									className="btn-link"
									onClick={() => {
										window.scroll(0, 0);
										navigate(`pharmacy-entries`);
									}}
								>
									Pharmacy Entries
								</p>
							</Dropdown.Item>

							<Dropdown.Item>
								<p
									className="btn-link"
									onClick={() => {
										window.scroll(0, 0);
										navigate(`pos`);
									}}
								>
									Pharmacy POS
								</p>
							</Dropdown.Item>
						</Dropdown.Menu>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default PharmacyDash;
